<template>
    <van-overlay :show="loading" class-name="loading-panel" >
        <div>
            <van-loading type="spinner" />
        </div>
    </van-overlay>
    <van-notice-bar
        wrapable
        :scrollable="false"
        text="仅用来测试，请谨慎使用"
    />
    <div>
        <van-form @submit="onSubmit">
            <div class="type-switch">
                <van-switch v-model="isText" inactive-color="#ff976a" />
            </div>
            <van-cell-group>
                <van-field
                    v-model="key"
                    name="Key"
                    label="密码"
                    placeholder="Key"
                    maxlength="16"
                />
                <van-field
                    v-model="iv"
                    name="Iv"
                    label="Iv"
                    placeholder="Iv"
                    maxlength="16"
                />
            </van-cell-group>
            <div v-if="isText" class="type-text">
                <van-field
                    v-model="text"
                    rows="3"
                    autosize
                    label="文本"
                    type="textarea"
                    placeholder="Text"
                />
                <div class="text-result" v-if="textresult">
                    <p>{{ textresult }}</p>
                </div>
            </div>
            <div class="type-file" v-else>
                <div class="file-wrapper">
                    <van-uploader :after-read="afterRead" :before-delete="beforeDelete" v-model="fileList" :max-count="1" accept="image/*,audio/*,video/*,text/*,application/*,.zip,.rar,.ppt,.pptx" />
                    <p v-if="fileList[0]">
                        {{formatSize(fileList[0].file.size)}}
                    </p>
                </div>
                <div class="file-result" v-if="fileresult && fileList.length">
                    <van-cell-group>
                        <van-cell>
                            <van-field
                                v-model="filename"
                                label="文件名"
                                placeholder="请输入文件名"
                                rows="3"
                                type="textarea"    
                            />
                        </van-cell>
                        <van-cell v-if="isDecode" class="media-wrapper">
                            <div v-if="filename.match(/\.(jpg|png|jpeg|gif)$/i)" style="text-align:center;">
                                <img :src="fileresult" @click="previewResult" />
                            </div>
                            <div v-if="filename.match(/\.(mov|mp4|mpeg|avi|wmv|flv|rmvb|arm|mp3)$/i)">
                                <video :src="fileresult" controls="controls" preload />
                            </div>
                        </van-cell>
                        <van-cell>
                            <a :href="fileresult" :download="filename" target="_blank" class="download">下载<span>({{formatSize(fileresultblob.size)}})</span></a>
                        </van-cell>
                    </van-cell-group>
                </div>
            </div>
        </van-form>
    </div>
    <div class="btn-group">
        <van-row>
            <van-col span="12">
                <van-button type="primary" class="btn" @click="checkRules('encode')">加密</van-button>
            </van-col>
            <van-col span="12">
                <van-button type="default" class="btn" @click="checkRules('decode')">解密</van-button>
            </van-col>
        </van-row>
    </div>
    <div class="rest-panel">
        <a href="javascript:;" @click="resetData">重置</a>
    </div>
</template>

<script>
import { defineComponent } from 'vue';
import { Notify, ImagePreview } from 'vant';
const wasm = import('wasm-aeslib');
import { text2Uint8Array, file2ArrayBuffer } from '/@/lib';

const InitData = {
    isText: false,
    key: '1234567812345678',
    iv: '1234567812345678',
    text: '',
    textresult: '',
    fileresult: '',
    fileresultblob: null,
    filename: '',
    originSize: '-',
    isDecode: false,
    fileList: [

    ],
    fileListResult: []
};

export default defineComponent({
    name: 'Home',
    data(){
        return {
            loading: false,
            ...InitData
        };
    },
    methods: {
        resetData(){
            location.reload();
        },
        beforeDelete(){
            this.fileresult = '';
            this.fileresultblob = '';
            return true;
        },
        afterRead({ file }) {
            console.log(file);
            this.fileList = [
                {url: window.URL.createObjectURL(file),  file, isImage: true}
            ];
        },
        onSubmit() {
            void(0);
        },
        formatSize(size) {
            return size>(1024*1024)? (size/1024/1024).toFixed(2) + 'Mb': (size/1024).toFixed(2)+ 'Kb';
        },
        checkRules(method){
            const isText = this.isText;
            if (this.key.length !== 16 || this.iv.length !== 16){
                return Notify({ type: 'warning', message: '请输入正确的密码' });
            }
            //
            if(isText && !this.text){
                return Notify({ type: 'warning', message: '请输入文字' });
            }
            if (!isText && !this.fileList.length) {
                return Notify({ type: 'warning', message: '请上传文件' });
            }
            //
            this.loading= true;
            if ('encode' === method) {
                return this.encode();
            } else {
                return this.decode();
            }
        },
        setFileName(type){
            if(this.fileList.length){
                const file = this.fileList[0].file;
                const name = file.name.replace(/.\w*$/, ($1) => {
                    return `-${type==='en'? 'en': 'de'}${$1}`;
                });
                this.filename = name;
            }
            return 0;
        },
        async encode(){
            const aeslib = await wasm.then(({ Aeslib }) => {
                return new Aeslib(this.key, this.iv);
            });
            this.isDecode= false;
            if(this.isText){
                const textU8Arr = await text2Uint8Array(this.text);
                const textU8ArrEncoded = aeslib.base64encode(textU8Arr);
                this.textresult = textU8ArrEncoded;
            }else{
                const fileArray = await file2ArrayBuffer(this.fileList[0].file);
                const newResult = aeslib.fileencode(new Uint8Array(fileArray) );
                const blob = new Blob([newResult.buffer]);
                this.fileresult = window.URL.createObjectURL(blob);
                this.fileresultblob = blob;
                this.setFileName('en');
            }
            this.loading = false;
        },
        async decode(){
            const aeslib = await wasm.then(({ Aeslib }) => {
                return new Aeslib(this.key, this.iv);
            });
            this.isDecode = true;
            if(this.isText){
                try{
                    const textU8ArrEncoded = aeslib.base64decode(this.text);
                    if(textU8ArrEncoded.length === 0){
                        this.loading = false;
                        Notify('解密失败，请检查参数');
                    }
                    this.textresult = textU8ArrEncoded;
                }catch(e){
                    this.loading = false;
                    console.log(e);
                }
            }else{
                const fileArray = await file2ArrayBuffer(this.fileList[0].file);
                const newResult = aeslib.filedecode(new Uint8Array(fileArray) );
                if(newResult.length === 0){
                    this.loading = false;
                    return Notify('解密失败，请检查文件或者参数');
                }
                const blob = new Blob([newResult.buffer]);
                this.fileresult = window.URL.createObjectURL(blob);
                this.fileresultblob = blob;
                this.fileListResult = [
                    {src: this.fileresult, isImage: true}
                ];
                this.setFileName('de');
            }
            this.loading = false;
        },
        previewResult(){
            ImagePreview({
                images: [this.fileresult],
                closeable: true
            });
        }
    },
    components: {
        
    },
    mounted(){
        //
    }
});
</script>

